"use strict"

export function initUtils() {
   slidersInit();
}

// ===========================================================================================
// -----------------------------
// current-page
// -----------------------------
document.addEventListener("DOMContentLoaded", function () {
   const currentUrl = window.location.href;
   const navLinks = document.querySelectorAll('.menu-header__link');

   navLinks.forEach(link => {
      const linkHref = link.getAttribute('href');

      // 1. –Ü–≥–Ω–æ—Ä—É—î–º–æ –ø–æ—Å–∏–ª–∞–Ω–Ω—è, —è–∫—ñ —î –ø—Ä–æ—Å—Ç–æ –∑–∞–≥–ª—É—à–∫–∞–º–∏ –¥–ª—è –º–µ–Ω—é (#)
      if (linkHref === "#" || linkHref === "") {
         return;
      }

      // 2. –Ü–≥–Ω–æ—Ä—É—î–º–æ –≥–æ–ª–æ–≤–Ω—É —Å—Ç–æ—Ä—ñ–Ω–∫—É (—è–∫—â–æ URL –∑–∞–∫—ñ–Ω—á—É—î—Ç—å—Å—è –Ω–∞ / –∞–±–æ index.html)
      // –í–∏–¥–∞–ª—ñ—Ç—å —Ü—é —É–º–æ–≤—É, —è–∫—â–æ –Ω–∞ –≥–æ–ª–æ–≤–Ω—ñ–π –≤—Å–µ –∂ —Ç–∞–∫–∏ –ø–æ—Ç—Ä—ñ–±–Ω–∞ –ø—ñ–¥—Å–≤—ñ—Ç–∫–∞
      const isHomePage = window.location.pathname === "/" || window.location.pathname.endsWith("index.html");
      if (isHomePage) {
         return;
      }

      // 3. –ü–æ—Ä—ñ–≤–Ω—é—î–º–æ URL –¥–ª—è –≤—Å—ñ—Ö —ñ–Ω—à–∏—Ö —Å—Ç–æ—Ä—ñ–Ω–æ–∫
      if (link.href === currentUrl) {
         link.classList.add('current-page');
      }
   });
});

