"use strict"

// –ó–∞–ø—É—Å–∫ –ø—Ä–∏ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—ñ —Å—Ç–æ—Ä—ñ–Ω–∫–∏
document.addEventListener('DOMContentLoaded', initModal);

export function openModal() {
   initModal()
}
// ===========================================================================================
async function initModal() {
   const openButtons = document.querySelectorAll('.open-btn-modal')
   const modalOverlay = document.querySelector('#modal-overlay')
   const modalContent = document.querySelector('#modal-content')
   const fixedElements = document.querySelectorAll('.header, .fixed-element')

   if (!openButtons.length || !modalOverlay || !modalContent) return

   function getScrollbarWidth() {
      return window.innerWidth - document.documentElement.clientWidth
   }

   function openModal() {
      const scrollbarWidth = getScrollbarWidth()
      if (scrollbarWidth > 0) {
         document.body.style.paddingRight = `${scrollbarWidth}px`
         fixedElements.forEach(el => el.style.paddingRight = `${scrollbarWidth}px`)
      }

      document.body.classList.add('no-scroll')
      modalOverlay.classList.add('is-open')
      modalOverlay.setAttribute('aria-hidden', 'false')
   }

   function closeModal() {
      modalOverlay.classList.remove('is-open')
      modalOverlay.setAttribute('aria-hidden', 'true')
   }

   // –ü–æ–≤–µ—Ä–Ω–µ–Ω–Ω—è scroll –ø—ñ—Å–ª—è –∑–∞–∫—Ä–∏—Ç—Ç—è —á–µ—Ä–µ–∑ transitionend
   modalOverlay.addEventListener('transitionend', () => {
      if (!modalOverlay.classList.contains('is-open')) {
         document.body.classList.remove('no-scroll')
         document.body.style.paddingRight = ''
         fixedElements.forEach(el => el.style.paddingRight = '')
      }
   });

   // –í—ñ–¥–∫—Ä–∏—Ç—Ç—è –Ω–∞ –≤—Å—ñ –∫–Ω–æ–ø–∫–∏
   openButtons.forEach(btn => {
      btn.addEventListener('click', async (e) => {
         e.preventDefault()

         if (!modalContent.innerHTML.trim()) {
            try {
               const response = await fetch('modal.html')
               if (!response.ok) throw new Error('Network response was not ok')
               modalContent.innerHTML = await response.text()
            } catch (err) {
               console.error('Modal Load Error:', err)
               modalContent.innerHTML = '<div style="padding:20px; text-align:center;">–ü–æ–º–∏–ª–∫–∞ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è —Ñ–æ—Ä–º–∏.</div>'
            }
         }

         openModal()
      })
   })

   // –ó–∞–∫—Ä–∏—Ç—Ç—è overlay –∞–±–æ –∫–Ω–æ–ø–∫–∏
   modalOverlay.addEventListener('click', (e) => {
      if (e.target === modalOverlay || e.target.closest('[data-modal-close]')) {
         closeModal()
      }
   });

   // –ó–∞–∫—Ä–∏—Ç—Ç—è –Ω–∞ Escape
   document.addEventListener('keydown', (e) => {
      if (e.key === 'Escape' && modalOverlay.classList.contains('is-open')) {
         closeModal()
      }
   });

   // Accessibility
   modalOverlay.setAttribute('role', 'dialog')
   modalOverlay.setAttribute('aria-modal', 'true')
   modalOverlay.setAttribute('aria-hidden', 'true')
}








